body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

.divider:after, .divider:before {
  content: "";
  height: 1px;
  background: #000;
  flex: 1;
}

.aboutus-page {
  border: 1px solid black min-height 100vh;
}

.my-modal {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.my-modal-content {
  width: 80%;
  background-color: #fff;
  border: 1px solid #888;
  margin: 10% auto;
  padding: 20px;
}

.my-close {
  color: #aaa;
  float: right;
  cursor: pointer;
  font-size: 28px;
  font-weight: bold;
}

.my-close:hover, .my-close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

#openModalBtn {
  z-index: 1;
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  padding: 10px;
  position: absolute;
  top: 10px;
  left: 10px;
}

